:root {
  --maincolor: #00acdc;
  --header-height: 60px;
  --header-margin: 50px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;

  & .logo {
    margin-left: var(--header-margin);
    align-items: center;
    display: flex;
  }

  & .nav {
    margin: 0 var(--header-margin) 0 0;
  }

  & .nav ul {
    justify-content: center;
    align-items: center;
    padding: 0;
    list-style: none;
    display: flex;

    & :not(:last-child) {
      margin-right: 20px;
    }

    & .nav__item {
      justify-content: center;
      align-items: center;
      display: flex;
    }

    & .nav__item a.is-active:after {
      content: "";
      background-color: var(--maincolor);
      border-radius: 8px;
      width: 100%;
      height: 4px;
      position: absolute;
      bottom: 0;
    }

    & .nav__item a {
      text-transform: uppercase;
      color: #000;
      height: var(--header-height);
      align-items: center;
      font-size: 12px;
      font-weight: 500;
      text-decoration: none;
      display: flex;
      position: relative;
    }

    & .nav__item a:hover, & .nav__item a.is-active {
      color: var(--maincolor);
    }
  }
}
